import React, { Component, Suspense } from 'react'; 
import '../styles/Modal.scss';
const AccioAnimation = React.lazy(() => import('./AccioAnimation'));

class Modal extends Component {
    constructor(props) {
        super(props)
        const LanguageData = {
            "English": {
                Loading: "Loading..."
            },
            "Chinese": {
                Loading: "加载中..."
            }
        }
        this.state = {
            modalInnerIsShown: false,
            animationIsPlayed: false,
            LanguageData:LanguageData
        }
    }

    toggleModalInner = (boolean) => {
        this.setState({modalInnerIsShown: boolean})
    }

    toggleAnimationIsPlayed= (boolean) => {
        this.setState({animationIsPlayed: boolean})
    }

    render() {
        
        const {ModalIsOpen,CurrentLanguage} = this.props;
        const {modalInnerIsShown, animationIsPlayed,LanguageData} = this.state;
        const currentLanguage = LanguageData[CurrentLanguage];
        return (
            <section className={"modal page-section page-section--small-when-mobile"+(ModalIsOpen ? " modal--open" : "")}>
                {(ModalIsOpen && !animationIsPlayed)? 
                    <Suspense fallback={<div className="modal__suspense__message">{currentLanguage.Loading}</div>}>
                        <AccioAnimation CurrentLanguage={CurrentLanguage} toggleRevealingItem={this.toggleModalInner} toggleAnimationIsPlayed={this.toggleAnimationIsPlayed}/>
                    </Suspense> 
                    : null
                }
                <div className={"modal__inner" + (modalInnerIsShown ? " modal__inner-is-shown": "")}>
                    <div className="wrapper">
                        {this.props.children}
                    </div>
                </div>
                <div className="modal__close" onClick={this.props.toggleModal}>X</div>
            </section>
        )
    }
}

export default Modal;