import React, { Component } from 'react';
import "../styles/footer.scss";


class Footer extends Component {
    constructor(props) {
        super(props);

        const LanguageData = {
            "English": {
                title: "To leave a comment, correct a sentence or report a bug.",
                buttonText: "Accio ~ Feedback Form" 
            },
            "Chinese": {
                title: "留言，反映bug，或者提供建议。请点击",
                buttonText: "留言表" 
            }
        }
        this.state = {
            LanguageData: LanguageData
        }
    }
    render() {
        const {LanguageData}= this.state
        const currentLanguage = LanguageData[this.props.CurrentLanguage]
        
        return (
            <footer className="footer">
                <div className="wrapper">
                    <p className="footer__paragraph"><span className="footer__text">{currentLanguage.title}</span>
                    <button className="btn btn--dark btn--medium btn--dark-shutter-effect" onClick={this.props.toggleFeedbackModal}>{currentLanguage.buttonText}</button>
                    </p>
                </div>
            </footer>
        )
    }
}

export default Footer;