import React, { Component } from 'react'; 
import Modal from './Modal';
import LetterForm from './LetterForm'; 


class ContactModal extends Component {
    constructor(props) {
        super(props);
        const LanguageData = {
            "English": {
                headline: "My Contact",
                description: {
                    part1: `I can be reached at `,
                    part2: "+86 15546131588",
                    part3: ` or by email, skype, or an owl. Details are down below!`,
                    part4: `Email: `,
                    part5: `liyinduo@gmail.com`,
                    part6: `Skype: `,
                    part7: `LIYINDUO`,
                    part8: `Send a letter through my owl: `,
                    part9: "Send a letter"
                } 
            },
            "Chinese": {
                headline: "联系方式",
                description: {
                    part1: `您可以通过我的手机号码联系我：`,
                    part2: "+86 15546131588",
                    part3: ` 或者通过邮件, Skype, 和下边的留言功能。`,
                    part4: `我的信箱: `,
                    part5: `liyinduo@gmail.com`,
                    part6: `Skype: `,
                    part7: `LIYINDUO`,
                    part8: `留言页面需点击进入: `,
                    part9: "留言"
                } 
            }
        }
        this.state={
            LetterFormIsVisible: false,
            LanguageData: LanguageData
        };
    }

    toggleLetterForm = (boolean) => {
        this.setState({LetterFormIsVisible: boolean})
    }
    
    render() {
        const {ContactModalIsOpen,CurrentLanguage} = this.props;
        const {LetterFormIsVisible,LanguageData} = this.state;
        const currentLanguage = LanguageData[CurrentLanguage]
        return(
            <Modal CurrentLanguage={CurrentLanguage} toggleModal= {this.props.toggleContactModal} ModalIsOpen={ContactModalIsOpen}> 
                <h2 className="headline">{currentLanguage.headline}</h2>
                <hr className="headline__styling-breakbar"/>
                <p className={"headline__description" + (LetterFormIsVisible ? " headline__description-is-hide": " headline__description-is-shown")}> 
                {currentLanguage.description.part1}
                <b>{currentLanguage.description.part2}</b>
                {currentLanguage.description.part3}<br/>
                {currentLanguage.description.part4}
                <b>{currentLanguage.description.part5}</b><br />
                {currentLanguage.description.part6}
                <b>{currentLanguage.description.part7}</b><br />
                {currentLanguage.description.part8}<br /> 
                <button className="btn btn--long btn--dark" onClick={()=>(this.toggleLetterForm(true))}>{currentLanguage.description.part9}</button>
                </p>
                <LetterForm CurrentLanguage={CurrentLanguage} LetterFormIsVisible={LetterFormIsVisible} toggleLetterForm={this.toggleLetterForm} RequiredFields={['Name', 'Email', 'Message']}/>
            </Modal>
        )
    }

}

export default ContactModal;