import React, { Component } from 'react'
import {Slide} from "react-reveal";
import "../styles/Stats.scss";
import NodeData from "../data/StatsObjectData.js";
import * as d3 from "d3";

class Stats extends Component {
    constructor(props) {
        super(props);

        const LanguageData = {
            "English": {
                headline: "My Stats"
            },
            "Chinese": {
                headline: "基本信息"
            }
        }
        this.state = {
            LanguageData: LanguageData
        }
    }

    componentWillReceiveProps(nextProps) {
        if(this.props.CurrentLanguage !== nextProps.CurrentLanguage) 
        {
            const nodes = NodeData[nextProps.CurrentLanguage]['nodes']
            const links = [
                {source: "Front-End Skills", target: "Automation"},
                {source: "Front-End Skills", target: "Organisation"},
                {source: "Front-End Skills", target: "Animation"},
                {source: "Front-End Skills", target: "Optimization"}
            ]
            this.linkSelection.data(links).exit().remove().enter();
            this.nodeSelection.data(nodes).exit().remove().enter();
            this.simulation.nodes(nodes);
            this.simulation.force('nodes', d3.forceManyBody().strength(-10));
            this.simulation.force('links').links(links);
            this.simulation.alpha(1).restart();
        }
    } 

    componentDidMount() {
        const nodes = NodeData[this.props.CurrentLanguage]['nodes']
        this.links = [
            {source: "Front-End Skills", target: "Automation"},
            {source: "Front-End Skills", target: "Organisation"},
            {source: "Front-End Skills", target: "Animation"},
            {source: "Front-End Skills", target: "Optimization"}
        ]
    
         const svg = d3.select(".Stats__SVG").attr("viewBox", "0 0 400 400")

         this.linkSelection = svg.selectAll("line")
                                  .data(this.links)
                                  .enter()
                                  .append("line")
                                    .attr("stroke", "black")
                                    .attr("stroke-width", 1)

         this.nodeSelection = svg.selectAll("circle")
                                  .data(nodes)
                                  .enter()
                                  .append("circle")
                                    .attr("r", d => d.size)
                                    .attr("fill", d => d.color)
                                    .attr("stroke", "black")
                                    .attr("stroke-width", 3)
                                    .attr("class", "Stats__SVG__circle")
                                    .call(d3.drag()
                                            .on("start", (d) => {
                                                this.simulation.alphaTarget(0.5).restart()
                                                d.fx = d.x
                                                d.fy = d.y
                                            })
                                            .on("drag", (d) => {
                                                // simulation.alpha(0.5).restart()
                                                d.fx = d3.event.x
                                                d.fy = d3.event.y
                                            })
                                            .on("end", (d) => {
                                                this.simulation.alphaTarget(0)
                                                d.fx = null
                                                d.fy = null
                                            }))
                                    .on("mousemove touchmove", this.showTooltip)
                                    .on("mouseout touchend", this.hideTooltip);

         this.simulation = d3.forceSimulation(nodes)
         this.simulation.force('center', d3.forceCenter(200,200))
                   .force('nodes', d3.forceManyBody().strength(-18))
                   .force('links', d3.forceLink(this.links)
                                     .id(d => d.concept)
                                     .distance(d => 5 * (d.source.size + d.target.size)))
                   .on('tick', () => {
                       this.nodeSelection
                        .attr('cx', d => d.x)
                        .attr('cy', d => d.y);
                    
                       this.linkSelection   
                        .attr("x1", d => d.source.x)
                        .attr("y1", d => d.source.y)
                        .attr("x2", d => d.target.x)
                        .attr("y2", d => d.target.y)
                   });

    }

    showTooltip(d) {
        var tooltip = d3.select(".tooltip");
        tooltip
            .style("opacity", 1)
            .style("left", '50%')
            .style("top", '1%')
            .style("transform", 'translateX(-50%)')
            .html(() => {
              const description = (d.description === "" ? "" : `<p>${d.description}</p>`)
              return `
                <p><b>${d.conceptDescription}</b></p>
                ${description}
              `
            });
    }
    
    hideTooltip() {
        d3.select(".tooltip")
            .style("opacity", 0);
    }

    render() {
        const {LanguageData} = this.state
        const {CurrentLanguage} = this.props
        const currentLanguage = LanguageData[CurrentLanguage]
        return (
            <section className="page-section">
                <div className="wrapper">
                    <Slide bottom cascade>    
                        <h2 className="headline">{currentLanguage.headline}</h2>
                        <hr className="headline__styling-breakbar"/>
                    </Slide>
                    <div className="Stats__SVG__Container">
                        <div className="tooltip"/>
                        <svg className="Stats__SVG">
                        </svg>
                    </div>
                </div>
            </section>
        )
    }
}

export default Stats;