import React, { Component } from 'react';
import Cover from './Cover';
import Header from "./Header";
import Footer from './Footer';
import Reminder from './Reminder';
import Work from './Work';
import Education from "./Education";
import Stats from "./Stats";
import Portkey from './Portkey';
import ContactModal from './ContactModal';
import FeedbackModal from './FeedbackModal';
import LazyLoad from 'react-lazyload';


class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      HeaderIsSlide: {HeaderSlideDown: false, HeaderSlideUp: false},
      HeaderIsBackToTop: false,
      LinksIsCurrentPosition: {MyGoal: false, MyWork: false, MyEdu: false, MyStats: false},
      ContactModalIsOpen: false,
      FeedbackModalIsOpen: false,
      Language: {English: true, Chinese: false}
    }
  }

  toggleHeaderSlide = (downBool,upBool) => {
    this.setState(prevState => ({HeaderIsSlide: {HeaderSlideDown: downBool, HeaderSlideUp: upBool}} ));
  }

  toggleHeaderIsBackToTop = (IsBackToTop) => {
    this.setState(prevState => ({HeaderIsBackToTop: IsBackToTop}));
  }

  setLinksIsCurrentPosition = (LinkObject) => {
    let links = {MyGoal: false, MyWork: false, MyEdu: false, MyStats: false}
    if(links[LinkObject] !== undefined) {
      links[LinkObject] = true
    }
    this.setState({LinksIsCurrentPosition: links});
  }

  toggleContactModal = () => {
    this.setState(prevState => ({ContactModalIsOpen: !prevState.ContactModalIsOpen}));
  }
  
  toggleFeedbackModal = () => {
    this.setState(prevState => ({FeedbackModalIsOpen: !prevState.FeedbackModalIsOpen}));
  }

  toggleLanguage = (value) => {
    let LanguageObject = {English: false, Chinese: false}
    if(LanguageObject[value] !== undefined) {
      LanguageObject[value] = true
      this.setState({Language: LanguageObject})
    }
  }
  
  render() {
    const {HeaderIsSlide,HeaderIsBackToTop,LinksIsCurrentPosition,ContactModalIsOpen,FeedbackModalIsOpen,Language} = this.state;
    const currentLanguage = Object.keys(Language).find(key => Language[key])
    return (
      <div className="App">
        <Header CurrentLanguage={currentLanguage} toggleLanguage={this.toggleLanguage} LinksIsCurrentPosition={LinksIsCurrentPosition} HeaderIsBackToTop={HeaderIsBackToTop} HeaderIsSlide={HeaderIsSlide} toggleContactModal={this.toggleContactModal}/>
        <Cover CurrentLanguage={currentLanguage}/>
        <Portkey id={'MyGoal'} first={true} before={''} after={'MyGoal'} 
                 setLinksIsCurrentPosition = {this.setLinksIsCurrentPosition}
                 toggleHeaderIsBackToTop={this.toggleHeaderIsBackToTop} 
                 toggleHeaderSlide={this.toggleHeaderSlide}/>
        <Reminder CurrentLanguage={currentLanguage}/>
        <Portkey id={'MyWork'} before={'MyGoal'} after={'MyWork'} toggleHeaderSlide={this.toggleHeaderSlide} setLinksIsCurrentPosition={this.setLinksIsCurrentPosition}/>
        <LazyLoad height={1259} offset={100} once>
          <Work CurrentLanguage={currentLanguage}/>
        </LazyLoad>
        <Portkey id={'MyEdu'} before={'MyWork'} after={'MyEdu'} toggleHeaderSlide={this.toggleHeaderSlide} setLinksIsCurrentPosition={this.setLinksIsCurrentPosition}/>
        <LazyLoad height={1362} offset={100} once>
          <Education CurrentLanguage={currentLanguage}/>
        </LazyLoad>
        <Portkey id={'MyStats'} before={'MyEdu'} after={'MyStats'} toggleHeaderSlide={this.toggleHeaderSlide} setLinksIsCurrentPosition={this.setLinksIsCurrentPosition}/>
        <LazyLoad height={753} offset={100} once>
          <Stats CurrentLanguage={currentLanguage}/>
        </LazyLoad>
        <div style={{width:'100%', height: '10px', backgroundColor:'black'}}/> 
        <ContactModal CurrentLanguage={currentLanguage} ContactModalIsOpen={ContactModalIsOpen} toggleContactModal={this.toggleContactModal}/>
        <FeedbackModal CurrentLanguage={currentLanguage} FeedbackModalIsOpen={FeedbackModalIsOpen} toggleFeedbackModal={this.toggleFeedbackModal}/>
        <Footer CurrentLanguage={currentLanguage} toggleFeedbackModal={this.toggleFeedbackModal}/>
      </div>
    );
  }
}

export default App;
