import React, {Component} from 'react';
import "../styles/Work.scss";
import {Slide,Fade} from "react-reveal";
import Workdata from "../data/WorkObjectData";
import { CSSTransition, TransitionGroup} from 'react-transition-group';

class Work extends Component {
    constructor(props) {
        super(props)

        const LanguageData = {
            "English": {
                headline: "My Work",
                buttonLeft: "Prev",
                buttonRight: "Next"
            },
            "Chinese": {
                headline: "工作经历",
                buttonLeft: "上一个",
                buttonRight: "下一个"
            }
        }

        this.state = {
            WorkProperyIndex: 0,
            WorkAutoplay: true,
            LanguageData: LanguageData
        }
    }  
    nextWorkProperty = () => {
        const newIndex = this.state.WorkProperyIndex+1;
        this.setState({ 
          WorkProperyIndex: newIndex,
          WorkAutoplay: false
        });
      }
    
    prevWorkProperty = () => {
          const newIndex = this.state.WorkProperyIndex-1;
          this.setState({
              WorkProperyIndex: newIndex,
              WorkAutoplay: false
          });
      }

    render() {
        const {WorkProperyIndex, WorkAutoplay, LanguageData} = this.state;
        const {CurrentLanguage} = this.props;
        const currentLanguage = LanguageData[CurrentLanguage]
        const WorkPropertyList = Workdata[CurrentLanguage]['properties'];
        const WorkProperties = WorkPropertyList[WorkProperyIndex];
        let workObject3;
        let Description;
        if(WorkProperties.ImageUrl4 !== undefined) {
            workObject3 = <div className="grid__el grid__el-large" style={{backgroundImage: `url(${require(`../images/${WorkProperties.ImageUrl4}`)})`}}></div>
        } else {
            workObject3 = <div className="grid__el grid__el-large">
                            <video className="grid__el-video" autoPlay={WorkAutoplay} muted loop controls={true}>
                                <source src={require(`../images/${WorkProperties.VideoUrl1}`)} type='video/mp4' />
                            </video>
                          </div>
        }
        if (WorkProperties.File !== undefined ) {
            let file = WorkProperties.File
            Description = <p className="headline__description">{WorkProperties.description} <a className="headline__anchor"href={`${require(`../data/${file.fileUrl}`)}`} download>{file.fileName}</a>
            </p>
        } else {
            Description = <p className="headline__description">{WorkProperties.description}</p>
        }
        return(
            <section className="Work page-section">
                <div className="wrapper">
                    <Slide bottom cascade>    
                        <h2 className="headline">{currentLanguage.headline}</h2>
                        <hr className="headline__styling-breakbar"/>
                    </Slide>
                    <Fade>
                        <div className="headline__buttons">
                        <button className={"btn btn--dark btn--small headline__button-left" + (WorkProperties.index === 0 ? " headline__button-disabled" : "")} onClick={this.prevWorkProperty} disabled={WorkProperties.index === 0}>{currentLanguage.buttonLeft}</button>
                        <button className={"btn btn--dark btn--small headline__button-right" + (WorkProperties.index === WorkPropertyList.length-1 ? " headline__button-disabled" : "")} onClick={this.nextWorkProperty} disabled={WorkProperties.index === WorkPropertyList.length-1}>{currentLanguage.buttonRight}</button>
                        </div>
                    </Fade>
                    <TransitionGroup>
                       <CSSTransition
                           key={WorkProperties._id}
                           classNames="Work__fade"
                           timeout={500}
                           >
                           <div>
                                    <p className="headline__title headline__title--has-buttons">{WorkProperties.title}</p>
                                    <Slide bottom fraction={0.1}>
                                            <div>
                                            {Description}
                                            <div className="grid">
                                                <div className="grid__el grid__el-small" style={{backgroundImage: `url(${require(`../images/${WorkProperties.ImageUrl1}`)})`}}></div>
                                                <div className="grid__el grid__el-large" style={{backgroundImage: `url(${require(`../images/${WorkProperties.ImageUrl2}`)})`}}></div>
                                                {workObject3}
                                                <div className="grid__el grid__el-small" style={{backgroundImage: `url(${require(`../images/${WorkProperties.ImageUrl3}`)})`}}></div>
                                            </div>
                                            </div>
                                    </Slide>
                           </div>
                       </CSSTransition>
                      </TransitionGroup>
                </div>
            </section>
        )
    }
}

export default Work;