import React, { Component } from 'react'; 
import Modal from './Modal';
import LetterForm from './LetterForm'; 

class FeedbackModal extends Component {
    constructor(props) {
        super(props);
        const LanguageData = {
            "English": {
                headline: "Feedback Form",
                description: {
                    part1: "Thank you for deciding to send me a feedback; I truly appreciate it.",
                    part2: "Please use the form below to send me a message.",
                    part3: "In case you wish to leave a message anonymously,",
                    part4: "I only set the message field as a required field.",
                    part5: "Send a feedback"
                } 
            },
            "Chinese": {
                headline: "留言与反馈",
                description: {
                    part1: "谢谢您选择给我留言，",
                    part2: "请点击使用下面的留言表。",
                    part3: "考虑到您可能想匿名填写建议,",
                    part4: "只有内容栏是必填的。",
                    part5: "继续"
                } 
            }
        }
        this.state={
            LetterFormIsVisible: false,
            LanguageData:LanguageData
        };
    }

    toggleLetterForm = (boolean) => {
        this.setState({LetterFormIsVisible: boolean})
    }
    
    render() {
        const {FeedbackModalIsOpen,CurrentLanguage} = this.props;
        const {LetterFormIsVisible, LanguageData} = this.state;
        const currentLanguage = LanguageData[CurrentLanguage];
        return(
            <Modal CurrentLanguage={CurrentLanguage} toggleModal= {this.props.toggleFeedbackModal} ModalIsOpen={FeedbackModalIsOpen}> 
                <h2 className="headline">{currentLanguage.headline}</h2>
                <hr className="headline__styling-breakbar"/>
                <p className={"headline__description" + (LetterFormIsVisible ? " headline__description-is-hide": " headline__description-is-shown")}> 
                    {currentLanguage.description.part1}<br/>
                    {currentLanguage.description.part2}<br/>
                    {currentLanguage.description.part3}<br/>
                    {currentLanguage.description.part4}<br/><br/>
                    <button className="btn btn--long btn--dark" onClick={()=>(this.toggleLetterForm(true))}>{currentLanguage.description.part5}</button>
                </p>
                <LetterForm CurrentLanguage={CurrentLanguage} LetterFormIsVisible={LetterFormIsVisible} toggleLetterForm={this.toggleLetterForm} RequiredFields={['Message']}/>
            </Modal>
        )
    }

}

export default FeedbackModal;