import React, { Component } from 'react'; 
import '../styles/LetterForm.scss';
import Fade from 'react-reveal/Fade';


class LetterForm extends Component {
    constructor(props) {
        super(props)
        this.LanguageData = {
            "English": {
                BasicInfo: {
                    Name: "Name",
                    Email: "Email",
                    Subject: "Subject",
                    Message: "Message",
                    Send: "Send"
                },
                InputOnFocusMessages: {
                    letter__form__name: {trigger: false, message: "Input your name here so I know how to address you."},
                    letter__form__email: {trigger: false, message: "Input your email here so I can get back to you quickly."},
                    letter__form__subject: {trigger: false, message: "Input the title of the Letter."},
                    letter__form__message: {trigger: false, message: "Input your message here, and don't worry about using the enter/return key; it won't trigger the send button."}
                }, 
                InputOnFocusAdditionalMessages: {
                    compulsaryMessage: `This field is required.`,
                    optionalMessage: `This field is optional.`
                },
                ButtonFeedbackMessages: {
                    fieldMissing: "field is empty.",
                    sendingMessage: "Sending... Please wait for a few seconds while I wake up my owl.",
                    succeededMessage: "Your message has been sent successfully.",
                    failedMessage: "Your message is lost in mid air; please inform me about this through email."
                }
            },
            "Chinese": {
                BasicInfo: {
                    Name: "姓名",
                    Email: "邮箱",
                    Subject: "标题",
                    Message: "内容",
                    Send: "发送",
                },
                InputOnFocusMessages: {
                    letter__form__name: {trigger: false, message: "填写以让我知道如何称呼您"},
                    letter__form__email: {trigger: false, message: "填写以让我快速地回覆您"},
                    letter__form__subject: {trigger: false, message: "在这里填写留言的标题"},
                    letter__form__message: {trigger: false, message: "请填写留言内容。不用担心使用回车键，不会触动发送按钮。"}
                },
                InputOnFocusAdditionalMessages: {
                    compulsaryMessage: `此栏需填。 `,
                    optionalMessage: `此栏非必填。`
                },
                ButtonFeedbackMessages: {
                    fieldMissing: "栏是空的。",
                    sendingMessage: "发送中... 请稍等片刻。",
                    succeededMessage: "您的信息已发送成功。",
                    failedMessage: "很抱歉，发送出现了问题，请稍后再试。"
                }
            }
        }
        const CurrentLanguage = this.LanguageData[this.props.CurrentLanguage]
        this.state={
            LetterFormIsVisible: this.props.LetterFormIsVisible,
            InputOnFocusMessages: CurrentLanguage.InputOnFocusMessages,
            InputOnFocusAdditionalMessages: CurrentLanguage.InputOnFocusAdditionalMessages,
            InputValues: {
                letter__form__name : "",
                letter__form__email : "",
                letter__form__subject : "",
                letter__form__message : ""
            },
            InputSubmitButton: {trigger: false, message: '', isDisabled: false},
            BasicInfo: CurrentLanguage.BasicInfo,
            ButtonFeedbackMessages: CurrentLanguage.ButtonFeedbackMessages
        }
    }
    componentWillReceiveProps(nextProps) {
        if(this.props.CurrentLanguage !== nextProps.CurrentLanguage) 
        {
            const newLanguage = nextProps.CurrentLanguage
            const CurrentLanguage = this.LanguageData[newLanguage]
            this.setState(()=>({
                BasicInfo: CurrentLanguage.BasicInfo,
                InputOnFocusAdditionalMessages: CurrentLanguage.InputOnFocusAdditionalMessages,
                InputOnFocusMessages: CurrentLanguage.InputOnFocusMessages,
                ButtonFeedbackMessages: CurrentLanguage.ButtonFeedbackMessages
            }),this.updateInputOnFocusMessagesBasedOnRequiredFields)

        }
    }

    componentDidMount() {
        this.updateInputOnFocusMessagesBasedOnRequiredFields();
    }

    updateInputOnFocusMessagesBasedOnRequiredFields = () => {
        let OnFocusMessageMap = new Map([
            ['Name', 'letter__form__name'],
            ['Email', 'letter__form__email'],
            ['Subject', 'letter__form__subject'],
            ['Message', 'letter__form__message']
        ])

        const requiredFields = this.props.RequiredFields
        const InputOnFocusMessages = this.state.InputOnFocusMessages
        let newMessages = {}
        const InputOnFocusAdditionalMessages = this.state.InputOnFocusAdditionalMessages
        if (requiredFields !== undefined) {
            for (let [key, value] of OnFocusMessageMap.entries()) {
                if (requiredFields.includes(key)) {
                    let newMessage = { trigger: false, message: `${InputOnFocusAdditionalMessages.compulsaryMessage} ${InputOnFocusMessages[value].message}` }
                    newMessages[value] = newMessage
                } else {
                    let newMessage = { trigger: false, message: `${InputOnFocusAdditionalMessages.optionalMessage} ${InputOnFocusMessages[value].message}` }
                    newMessages[value] = newMessage
                }
            }
            if (Object.keys(newMessages).length > 0) {
                this.setState(prevState => (
                    { InputOnFocusMessages: { ...prevState.InputOnFocusMessages, ...newMessages}}
                ))
            }
        }
    }

    handleChange = ({ target }) => {
        this.setState(prevState => ({
            InputValues:{...prevState.InputValues,[target.getAttribute('data-id')]: target.value } 
        }));
    }

    handleSubmitFeedback = (message) => {
        this.setState(() => (
            {InputSubmitButton: {trigger: true, message: message, isDisabled:false}}
             ), () => (
                setTimeout(() => (
                    this.setState({InputSubmitButton: {trigger: false, message: '', isDisabled:false}})  
                ),3000)
             )
        )
    }

    checkEmpty = (data) => {
        return data.split(' ').join('').length === 0
    }

    handelSubmit = ({currentTarget}) => {
        let {letter__form__name, letter__form__email, letter__form__subject, letter__form__message} = this.state.InputValues;
        
        let InputValuesMap = new Map([
            ['Name', letter__form__name],
            ['Email', letter__form__email],
            ['Subject', letter__form__subject],
            ['Message', letter__form__message]
        ])

        const requiredFields = this.props.RequiredFields
        if(requiredFields !== undefined) {
            for (let value of requiredFields) {
                if (InputValuesMap.has(value)) {
                    if(this.checkEmpty(InputValuesMap.get(value))){
                        this.handleSubmitFeedback(`${this.state.BasicInfo[value]} ${this.state.ButtonFeedbackMessages.fieldMissing}`)
                        return;
                    }
                }
            }
        }
        const ButtonFeedbackMessages = this.state.ButtonFeedbackMessages
        this.setState({
            InputSubmitButton: {trigger: true, message: ButtonFeedbackMessages.sendingMessage, isDisabled: true}
        })  

        let result = `name: ${letter__form__name}, email ${letter__form__email}, subject: ${letter__form__subject}, message: ${letter__form__message}`;
        fetch('https://yinduo-first-api.herokuapp.com/api/todos', {
            method: 'post',
            headers: new Headers({
                'Content-Type': 'application/json'
            }),
            body: JSON.stringify({name: result})
        })
         .then((data) => data.status)
         .then((status) => status === 201 ? true : false)
         .then((boolean) => (boolean ? this.handleSubmitFeedback(ButtonFeedbackMessages.succeededMessage) : this.handleSubmitFeedback(ButtonFeedbackMessages.failedMessage)))
        
    }

    onFocus = ({ target }) => {
        if (target.value.length > 0) return;
        let id = target.getAttribute('data-id');
        let triggerObject = Object.assign({}, this.state.InputOnFocusMessages[id]);
        triggerObject.trigger = true;
        this.setState(preState => (
            {InputOnFocusMessages: {...preState.InputOnFocusMessages, [id]:triggerObject}}
        ), () => {
           setTimeout(() => {
               triggerObject.trigger = false;
               return this.setState(preState => (
                   {InputOnFocusMessages: {...preState.InputOnFocusMessages, [id]:triggerObject}}
               ))
           },4000)
       });
    }

    closeLetterForm = () => {
        this.props.toggleLetterForm(false)
    }

    render() {
        const {LetterFormIsVisible} = this.props
        const {InputValues,InputOnFocusMessages,InputSubmitButton, BasicInfo} = this.state
        return (
            <form className={"letter__form" + (LetterFormIsVisible ? " letter__form-is-visible": "")}>
                <label>
                    <span>{BasicInfo.Name}</span>
                    <input type="text"
                            data-id="letter__form__name"
                            onFocus={this.onFocus}
                            value={InputValues.letter__form__name}
                            onChange={this.handleChange}
                    />
                </label>
                <Fade bottom collapse when={InputOnFocusMessages.letter__form__name.trigger}>
                    <div className="letter__form__Input-on-focus__messages">
                        {InputOnFocusMessages.letter__form__name.message}
                    </div>
                </Fade>
                <label>
                    <span>{BasicInfo.Email}</span>
                    <input type="email" 
                            data-id="letter__form__email"
                            onFocus={this.onFocus}
                            value={InputValues.letter__form__email}
                            onChange={this.handleChange}
                    />
                </label>
                <Fade bottom collapse when={InputOnFocusMessages.letter__form__email.trigger}>
                    <div className="letter__form__Input-on-focus__messages">
                        {InputOnFocusMessages.letter__form__email.message}
                    </div>
                </Fade>
                <label>
                    <span>{BasicInfo.Subject}</span>
                    <input type="text" 
                            data-id="letter__form__subject"   
                            onFocus={this.onFocus}
                            value={InputValues.letter__form__subject}
                            onChange={this.handleChange}
                    />
                </label>
                <Fade bottom collapse when={InputOnFocusMessages.letter__form__subject.trigger}>
                    <div className="letter__form__Input-on-focus__messages">
                        {InputOnFocusMessages.letter__form__subject.message}
                    </div>
                </Fade>
                <label>
                    <span>{BasicInfo.Message}</span>
                    <textarea
                        type="text"
                        data-id="letter__form__message"
                        value={InputValues.letter__form__message}
                        onChange={this.handleChange}
                        onFocus={this.onFocus}
                    />
                </label>
                <Fade bottom collapse when={InputOnFocusMessages.letter__form__message.trigger}>
                    <div className="letter__form__Input-on-focus__messages">
                        {InputOnFocusMessages.letter__form__message.message}
                    </div>
                </Fade>
                <div className="letter__form--mobile__view__messages">
                {InputSubmitButton.message}
                </div>
                <label htmlFor="letter__form__button-wrapper">
                    <span/>
                    <button id="letter__form__button" disabled={InputSubmitButton.isDisabled} className={"btn btn--small btn--dark letter__form__button"+ (InputSubmitButton.isDisabled ? " letter__form__button-is-disabled": "")} type="button" onClick={this.handelSubmit}>
                        {BasicInfo.Send}
                    </button>
                </label>
                <Fade bottom collapse when={InputSubmitButton.trigger}>
                    <div className="letter__form__Input-on-submit__messages">
                        {InputSubmitButton.message}
                    </div>
                </Fade>
                <div className="letter__form__close" onClick={this.closeLetterForm}>X</div>
             </form>
        )
    }
}

export default LetterForm;