import React, { Component } from 'react';
import "../styles/Header.scss";
import { Link } from 'react-scroll'

class Header extends Component {

    constructor(props) {
        super(props);

        const LanguageData = {
            "English": {
                MyGoal: "My Goal",
                MyWork: "My Work",
                MyEdu: "My Edu",
                MyStats: "My Stats",
                toggleContactModal: "Accio ~ Contact"
            },
            "Chinese": {
                MyGoal: "目标",
                MyWork: "工作",
                MyEdu: "教育",
                MyStats: "信息",
                toggleContactModal: "联络方式"
            }
        }
     
        this.state = {
            NavbarIsVisible: false,
            LanguageData: LanguageData
        }
    }

    toggleTheMenu = () => {
        this.setState(prevState => ({NavbarIsVisible: !prevState.NavbarIsVisible }));
    }

    toggleTheLanguage = ({target}) => {
        this.props.toggleLanguage(target.value)
    }

    render(){
        let {NavbarIsVisible,LanguageData} = this.state;
        let {HeaderSlideDown, HeaderSlideUp} = this.props.HeaderIsSlide;
        let {HeaderIsBackToTop, LinksIsCurrentPosition} = this.props;
        const CurrentLanguage = LanguageData[this.props.CurrentLanguage];

        return (
            <React.Fragment>
            <div className={(NavbarIsVisible ? "header__menu-icon header__menu-icon--close-x" : "header__menu-icon")+ (HeaderIsBackToTop ? " header__menu-icon--is-back-to-top": "")+ (HeaderSlideDown ? " header__menu-icon--slide-down": "") + (HeaderSlideUp ? " header__menu-icon--slide-up": "")} onClick={this.toggleTheMenu}>
                <div className="header__menu-icon__middle"></div>
            </div>
            <header className={("header")+(NavbarIsVisible? " header--is-expanded" : "") + (HeaderIsBackToTop ? " header--is-back-to-top": "")+ (HeaderSlideDown ? " header--slide-down": "") + (HeaderSlideUp ? " header--slide-up": "")}>
                <div className="header__container">
                  <div>
                    <div className="Patronum"/>
                  </div>
                  <div className={NavbarIsVisible ? "navbar__container navbar__container--is-visible" : "navbar__container"}>
                    <nav className="navbar">
                     <ul>
                         <li>
                            <Link className={LinksIsCurrentPosition.MyGoal ? "Navbar__anchor-is-current-link": ""} to="MyGoal" smooth="easeOutQuart" duration={1000}>
                                    {CurrentLanguage.MyGoal}
                            </Link>
                         </li>
                         <li>
                            <Link className={LinksIsCurrentPosition.MyWork ? "Navbar__anchor-is-current-link": ""} to="MyWork" smooth="easeOutQuart" duration={1000}>
                                    {CurrentLanguage.MyWork}
                            </Link>
                         </li>
                         <li>
                            <Link className={LinksIsCurrentPosition.MyEdu ? "Navbar__anchor-is-current-link": ""} to="MyEdu" smooth="easeOutQuart" duration={1000}>
                                    {CurrentLanguage.MyEdu}
                            </Link>
                         </li>
                         <li>
                            <Link className={LinksIsCurrentPosition.MyStats ? "Navbar__anchor-is-current-link": ""} to="MyStats" smooth="easeOutQuart" duration={1000}>
                                    {CurrentLanguage.MyStats}
                            </Link>
                         </li>
                     </ul>
                    </nav>
                     <select onChange={this.toggleTheLanguage} value={this.props.CurrentLanguage} className="navbar__language__select btn--dark">
                        <option value="English">English</option>
                        <option value="Chinese">中文 (CN)</option>
                     </select>
                    <button className="btn btn--dark btn--medium btn--dark-shutter-effect" onClick={this.props.toggleContactModal}>{CurrentLanguage.toggleContactModal}</button>
                  </div>
                </div>
            </header>
            </React.Fragment>
        )
    }
}

export default Header;