import React, {Component} from 'react';
import '../styles/Cover.scss';
// import global style sheet => wrapper. If not linked.

class Cover extends Component { 
    constructor(props) {
        super(props);

        const LanguageData = {
            "English": {
                title: `Hi, I'm Yínduó; a `,
                titleGlitch: "developer."
            },
            "Chinese": {
                title: `您好。我叫李吟铎，是一名`,
                titleGlitch: "程序员。"
            }
        }
        this.state = {
            LanguageData: LanguageData
        }
    }
    render() {
        const {LanguageData}= this.state
        const {CurrentLanguage} = this.props
        const currentLanguage = LanguageData[CurrentLanguage]
        
        return (
            <section className="Cover">
              <div className="Cover__Hero-Image"/>
              <div className="Cover__text-content">
                <div className="wrapper">
                     <h1 className="Cover__title">{currentLanguage.title}<span className={"Cover__title__glitch"+ (CurrentLanguage==="English" ? "-English": "-Chinese")}>{currentLanguage.titleGlitch}</span></h1>
                </div>
              </div>
            </section>
        )
    }
}

export default Cover;